.slogan {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  top: 40%;
  opacity: 0;
}

.slogan img {
  max-width: 900px;
  text-align: center;
}

.slogan img.inkan {
  max-width: 25px;
  width: 100%;
  padding-left: 20px;
}

.slogan:before {
  content: "";
  display: block;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #0a0a22;
  transform: translateZ(0) scaleX(0);
}

.slogan.is-shown {
  opacity: 1;
}

.slogan.is-shown:before {
  animation: mask 2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes mask {
  0% {
    transform-origin: left;
    transform: scaleX(0) translateZ(0);
    animation-timing-function: cubic-bezier(0.9, 0, 0.1, 1);
  }
  0.0001% {
    transform-origin: left;
  }
  50% {
    transform-origin: left;
    transform: scaleX(1) translateZ(0);
  }
  50.0001% {
    transform-origin: right;
  }
  100% {
    transform-origin: right;
    transform: scaleX(0) translateZ(0);
  }
}

@keyframes fadeInItem {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeInItem 0.5s linear both;
  opacity: 1;
}

.js-slogan.is-shown {
  animation: fadeInItem 0.5s linear both;
  opacity: 1;
}

.js-inkan {
  opacity: 0;
}

.js-inkan.is-shown {
  animation: fadeInItem 0.5s linear both;
  opacity: 1;
}

