@keyframes voice-pulse {
  0% {
    transform: scale(1) translateY(0);
  }
  5.55556% {
    transform: scale(0.9) translateY(5%) rotate(-8deg);
  }
  11.11111% {
    transform: scale(0.9) translateY(5%) rotate(-8deg);
  }
  16.66667% {
    transform: scale(1.3) translateY(-5%) rotate(8deg);
  }
  22.22222% {
    transform: scale(1.3) translateY(-5%) rotate(-8deg);
  }
  27.77778% {
    transform: scale(1.3) translateY(-5%) rotate(8deg);
  }
  33.33333% {
    transform: scale(1.3) translateY(-5%) rotate(-8deg);
  }
  38.88889% {
    transform: scale(1.3) translateY(-5%) rotate(8deg);
  }
  44.44444% {
    transform: scale(1) translateY(0) rotate(0);
  }
  100% {
    transform: scale(1) translateY(0) rotate(0);
  }
}

@keyframes voice-pulse-sp {
  0% {
    transform: scale(1) translateY(0);
  }
  5.55556% {
    transform: scale(0.95) translateY(5%) rotate(-8deg);
  }
  11.11111% {
    transform: scale(0.95) translateY(5%) rotate(-8deg);
  }
  16.66667% {
    transform: scale(1.1) translateY(-5%) rotate(8deg);
  }
  22.22222% {
    transform: scale(1.1) translateY(-5%) rotate(-8deg);
  }
  27.77778% {
    transform: scale(1.1) translateY(-5%) rotate(8deg);
  }
  33.33333% {
    transform: scale(1.1) translateY(-5%) rotate(-8deg);
  }
  38.88889% {
    transform: scale(1.1) translateY(-5%) rotate(8deg);
  }
  44.44444% {
    transform: scale(1) translateY(0) rotate(0);
  }
  100% {
    transform: scale(1) translateY(0) rotate(0);
  }
}

