.team-shishima {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 35%;
}

.team-shishima img {
  max-width: 200px;
  width: 100%;
  text-align: center;
  margin: auto;
}

.team-shishima a {
  position: relative;
  left: 0;
  right: 0;
  margin: auto;
  top: 39%;
  width: 100%;
  max-width: 250px;
  border-style: solid;
  border-width: 2px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.3s, background 0.3s, border 0.3s;
  will-change: color, background, border;
  border-color: #1a1a1a;
  padding: 5px 20px;
}

.team-shishima a:before, .team-shishima a:after {
  border-style: solid;
  box-sizing: border-box;
  content: "";
  display: block;
  height: 1em;
  pointer-events: none;
  position: absolute;
  transition: width 0.3s, height 0.3s, border 0.3s;
  width: 1em;
  will-change: width, height, border;
  border-color: #1a1a1a;
}

.team-shishima a:before {
  border-width: 2px 0 0 2px;
  left: -6px;
  top: -6px;
}

.team-shishima a:after {
  border-width: 0 2px 2px 0;
  bottom: -6px;
  right: -6px;
}

.team-shishima a:hover {
  border-color: #fff;
}

.team-shishima a:hover:before, .team-shishima a:hover:after {
  height: calc(100% + 12px);
  width: calc(100% + 12px);
  border-color: #fff;
}

